import { useEffect, useRef, useState } from 'react';
import { withPrefix } from 'gatsby';
import axios from 'axios';
import Cookies from 'js-cookie';
import algoliaSearch from 'algoliaSearch';

const algoliaIndex = algoliaSearch('V2FZ8PQPU1', '53dfa38126e32c85b534b72c3830168d').initIndex('helpmekaar');

const transformAlgoliaResults = algoliaObj => {
  return {
    results: algoliaObj.hits.map(page => ({
      title: page.title,
      description: page.description,
      url: page.url,
      tags: page.tags
    })),
    number: algoliaObj.nbHits
  };
};

const useChatState = () => {

  const windowGlobal = typeof window !== 'undefined' && window;
  const sessionStorageChatKey = 'expatty-chat';
  const sessionStorageSearchKey = 'expatty-search';
  const [messageQueue, setMessageQueue] = useState([]);
  const [dataSource, setDataSource] = windowGlobal && useState(windowGlobal.sessionStorage.getItem(sessionStorageChatKey)
    ? JSON.parse(windowGlobal.sessionStorage.getItem(sessionStorageChatKey))
    : []);
  const [isTyping, setTyping] = useState(false);
  const [sessionAttributes, setSessionAttributes] = windowGlobal && useState(windowGlobal.sessionStorage.getItem(sessionStorageSearchKey)
    ? JSON.parse(windowGlobal.sessionStorage.getItem(sessionStorageSearchKey))
    : {});
  const [userId] = useState(`pattybot-web${Date.now()}`);
  const [typeTimer, setTypeTimer] = useState([]);
  const [inputText, setInputText] = useState(null);
  const [newChatCount, setNewChatCount] = useState(null);

  const dataSourceRef = useRef(dataSource);
  dataSourceRef.current = dataSource;

  const typeTimerRef = useRef(typeTimer);
  typeTimerRef.current = typeTimer;

  const processQueue = () => {
    const [nextMessage, ...rest] = messageQueue;
    if (nextMessage.position === 'left') {
      if (isTyping) {
        return;
      }
      setMessageQueue(rest);
      setTyping(true);
      const thisTimer = setTimeout(() => {
        setDataSource([...dataSourceRef.current, nextMessage]);
        windowGlobal && windowGlobal.sessionStorage.setItem(sessionStorageChatKey, JSON.stringify(dataSourceRef.current));
        setTyping(false);
        clearTimeout(thisTimer);
        setTypeTimer(typeTimerRef.current.filter(t => t !== thisTimer));
      }, nextMessage.text ? nextMessage.text.length * 5 : 200);
      setTypeTimer([...typeTimer, thisTimer]);
    } else {
      setMessageQueue(rest);
      setTyping(false);
      typeTimer.forEach(t => clearInterval(t));
      setDataSource([...dataSource, nextMessage]);
      windowGlobal && windowGlobal.sessionStorage.setItem(sessionStorageChatKey, JSON.stringify(dataSourceRef.current));
      setInputText(nextMessage.text);
    }
  };

  const processSystemMessages = (msgList) => {
    setMessageQueue([...messageQueue, ...msgList]);
    setNewChatCount({
      count: msgList.length
    });
  }

  const getSystemMessageFromText = text => ({
    avatar: withPrefix('/img/patty_avatar_small.png'),
    position: 'left',
    title: 'Patty:',
    type: 'text',
    text,
    date: new Date(),
  });

  const processInput = text => {
    if (!text) {
      return;
    }
    setMessageQueue([{
      avatar: 'img/you-avatar.png',
      position: 'right',
      title: 'You:',
      type: 'text',
      text
    }]);

  };

  useEffect(() => {
    if (!windowGlobal) {
      return;
    }
    if (!windowGlobal.sessionStorage.getItem(sessionStorageChatKey)) {
      if (Cookies.get('expatty-visited')) {
        processSystemMessages([
          getSystemMessageFromText(`Oh, you're back! Anything I can help you with?`)
        ]);
      } else {
        processSystemMessages([
          getSystemMessageFromText(`Hola! I'm Patty.`),
          getSystemMessageFromText(`If you have questions about Andalusia, you've come to the right place.`),
          getSystemMessageFromText(`You can ask me a question, or just do a search.`),
          getSystemMessageFromText(`Just start typing in the box.`)
        ]);
      }
      Cookies.set('expatty-visited', true);
    }

  }, []);

  useEffect(() => {
    if (!inputText) {
      return;
    }
    console.log('Doing search');
    algoliaIndex.search(inputText).then(response => setSessionAttributes({
      searchResults: transformAlgoliaResults(response)
    }));
    /*algoliaIndex.search({
      query: inputText,
      page: 0,
      hitsPerPage: 500
    }, (searchError, searchResults) => {
      if (!searchError) {
        console.log(searchResults);
        setSessionAttributes({
          searchResults: transformAlgoliaResults(searchResults)
        });
      } else {
        console.error(searchError);
      }
    });*/



    /*axios.post('https://o1f9xnry71.execute-api.eu-west-1.amazonaws.com/prod/expattyChat', {
      inputText,
      userId,
      sessionAttributes: {}
    }).then(({ data }) => {
      setSessionAttributes(data.sessionAttributes);
      windowGlobal && windowGlobal.sessionStorage.setItem(sessionStorageSearchKey, JSON.stringify(data.sessionAttributes));
      const responseArray = data.message.startsWith('{') ? JSON.parse(data.message).messages.map(m => m.value) : [data.message];
      processSystemMessages(responseArray.map(response => getSystemMessageFromText(response)));
    }, console.error);*/
  }, [inputText]);

  if (messageQueue.length) {
    processQueue();
  }

  return {
    dataSource,
    processInput,
    isTyping,
    newChatCount,
    sessionAttributes: sessionAttributes || {}
  };

};

export default useChatState;